import React, { Component } from "react";
import evalFormDataService from "../evalform.service";

export default class EvalForm extends Component {
    constructor(props) {
        super(props);
        this.onChangeDisciplinary = this.onChangeDisciplinary.bind(this);
        this.onChangeIntellectual = this.onChangeIntellectual.bind(this);
        this.onChangeInterpersonal = this.onChangeInterpersonal.bind(this);
        this.onChangeGlobal = this.onChangeGlobal.bind(this);
        this.onChangePersonal = this.onChangePersonal.bind(this);
        this.onChangeContributions = this.onChangeContributions.bind(this);
        this.onChangeImprovement = this.onChangeImprovement.bind(this);
        this.onChangeStudent = this.onChangeStudent.bind(this);
        this.getEvalForm = this.getEvalForm.bind(this);
        this.updateSubmit = this.updateSubmit.bind(this);
        this.updateEvalForm = this.updateEvalForm.bind(this);
        this.deleteEvalForm = this.deleteEvalForm.bind(this);

        this.state = {
            currentEval: {
                DisciplinaryKnowledge_value: "",
                IntellectualCreativeSkills_value: "",
                InterpersonalSkills_value: "",
                GlobalCitizenship_value: "",
                PersonalMastery_value: "",
                Contributions_value: "",
                Contributionpercent_value: "",
                Improvement_value: "",
                StudentReciever_id: 0,

                submitted: false
            }
        };
    }

    componentDidMount() {
        this.getEvalForm(this.props.match.params.id);
    }

    onChangeDisciplinary(e) {
        const DisciplinaryKnowledge_value = e.target.value;

        this.setState(function(prevState) {
            return {
                currentEval: {
                    ...prevState.currentEval,
                    DisciplinaryKnowledge_value: DisciplinaryKnowledge_value
                }
            };
        });
    }

    onChangeIntellectual(e) {
        const IntellectualCreativeSkills_value = e.target.value;

        this.setState(prevState => {
            return {
                currentEval: {
                    ...prevState.currentEval,
                    IntellectualCreativeSkills_value: IntellectualCreativeSkills_value
                }
            };
        });
    }

    onChangeInterpersonal(e) {
        const InterpersonalSkills_value = e.target.value;

        this.setState(function(prevState) {
            return {
                currentEval: {
                    ...prevState.currentEval,
                    InterpersonalSkills_value: InterpersonalSkills_value
                }
            };
        });
    }

    onChangeGlobal(e) {
        const GlobalCitizenship_value = e.target.value;

        this.setState(prevState => {
            return {
                currentEval: {
                    ...prevState.currentEval,
                    GlobalCitizenship_value: GlobalCitizenship_value
                }
            };
        });
    }

    onChangePersonal(e) {
        const PersonalMastery_value = e.target.value;

        this.setState(prevState => {
            return {
                currentEval: {
                    ...prevState.currentEval,
                    PersonalMastery_value: PersonalMastery_value
                }
            };
        });
    }

    onChangeContributions(e) {
        const Contributions_value = e.target.value;

        this.setState(prevState => {
            return {
                currentEval: {
                    ...prevState.currentEval,
                    Contributions_value: Contributions_value
                }
            };
        });
    }

    onChangeImprovement(e) {
        const Improvement_value = e.target.value;

        this.setState(prevState => {
            return {
                currentEval: {
                    ...prevState.currentEval,
                    Improvement_value: Improvement_value
                }
            };
        });
    }

    onChangeStudent(e) {
        const StudentReciever_id = e.target.value;

        this.setState(prevState => {
            return {
                currentEval: {
                    ...prevState.currentEval,
                    StudentReceiver_id: StudentReciever_id
                }
            };
        });
    }

    getEvalForm(id) {
        evalFormDataService.get(id)
            .then(response => {
                this.setState({
                    currentEval: response.data
                });
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    updateSubmit(status) {
        var data = {
            DisciplinaryKnowledge_value: this.state.DisciplinaryKnowledge_value,
            IntellectualCreativeSkills_value: this.state.IntellectualCreativeSkills_value,
            InterpersonalSkills_value: this.state.InterpersonalSkills_value,
            GlobalCitizenship_value: this.state.GlobalCitizenship_value,
            PersonalMastery_value: this.state.PersonalMastery_value,
            Contributions_value: this.state.Contributions_value,
            Contributionpercent_value: this.state.Contributionpercent_value,
            Improvement_value: this.state.Improvement_value,
            StudentReciever_id: this.state.StudentReceiver_id,

            submitted: status
        };

        evalFormDataService.update(this.state.currentEval.id, data)
            .then(response => {
                this.setState(prevState => ({
                    currentEval: {
                        ...prevState.currentEval,
                        submitted: status
                    }
                }));
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    updateEvalForm() {
        evalFormDataService.update(
            this.state.currentEval.id,
            this.state.currentEval
        )
            .then(response => {
                console.log(response.data);
                this.setState({
                    message: "The Eval Form was updated successfully!"
                });
            })
            .catch(e => {
                console.log(e);
            });
    }

    deleteEvalForm() {
        evalFormDataService.delete(this.state.currentEval.id)
            .then(response => {
                console.log(response.data);
                this.props.history.push('/evalforms')
            })
            .catch(e => {
                console.log(e);
            });
    }

    render() {
        const { currentEval } = this.state;

        return (
            <div>
                {currentEval ? (
                    <div className="edit-form">
                        <h4>Eval Form</h4>
                        <form>
                            <h4>Eval Form</h4>
                            <div>
                                <label>
                                    <strong>Peer Evaluation For:</strong>
                                </label>{" "}
                                {currentEval.StudentReciever_id}
                            </div>
                            <div>
                                <label>
                                    <strong>Disciplinary Knowledge</strong>
                                </label>{" "}
                                {currentEval.DisciplinaryKnowledge_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Intellectual and Creative Skills</strong>
                                </label>{" "}
                                {currentEval.IntellectualCreativeSkills_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Interpersonal Skills</strong>
                                </label>{" "}
                                {currentEval.InterpersonalSkills_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Global Citizenship</strong>
                                </label>{" "}
                                {currentEval.GlobalCitizenship_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Personal Mastery</strong>
                                </label>{" "}
                                {currentEval.PersonalMaster_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Contributions</strong>
                                </label>{" "}
                                {currentEval.Contributions_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Contribution Percentage</strong>
                                </label>{" "}
                                {currentEval.Contributionpercent_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Improvement</strong>
                                </label>{" "}
                                {currentEval.Improvement_value}
                            </div>

                            <div className="form-group">
                                <label>
                                    <strong>Status: </strong>
                                </label>
                                {currentEval.submitted ? "Submitted" : "Not Submitted"}
                            </div>
                        </form>

                        {currentEval.status ? (
                            <button
                                className="badge badge-primary mr-2"
                                onClick={() => this.updateEvalForm(false)}
                            >Unpublish</button>
                        ) : (
                            <button 
                                className="badge badge-primary mr-2"
                                onClick={() => this.updateEvalForm(true)}
                            >Submit</button>
                        )}

                        <button
                            className="badge badge-danger mr-2"
                            onClick={this.deleteEvalForm}
                        >Delete</button>

                        <button
                            type="submit"
                            className="badge badge-success"
                            onClick={this.updateEvalForm}
                        >Submit</button>
                        <p>{this.state.message}</p>
                    </div>
                ) : (
                    <div>
                        <br/>
                        <p>Please click on an eval form.</p>
                    </div>
                )}
            </div>
        );
    }
}